@import url(https://fonts.googleapis.com/css?family=Montserrat:300,500,700,800&display=swap);
* {
    margin: 0;
    padding: 0;
    border: 0;
    font-family: 'Montserrat', sans-serif;
}

body {
    background-color: #F2F2F2;
    max-width: 100%;
    width: 100%;
    width: 100vw;
}
a{
    outline: none;
    cursor: none;
    text-decoration: none;
}
.container{
    width: 100%;
    width: 100vw;
    display: grid;
    grid-template-areas:
        'logo'
        'main'
        'image';
}

.logo {
    grid-area: logo;
    margin: auto;
}

.main { 
    grid-area: main;
    text-align: center;
}

.title {
    padding: 3vw;
    font-size: 3rem;
    font-weight: 800;
}

.image {
    grid-area: image;
}


img.main-image-mobile {
    max-width: 100vw;
}

img.main-image {
    visibility: hidden;
}

.logo-image{
    width: 50vw;
    padding: 5vw;
}

button {
    display: flex;
    justify-content: space-around;
    margin: auto;
    padding: 0.8rem;
    border-radius: 3rem;
    background-color: #15573F;
    color: #FFF;
    width: 10rem;
    font-weight: 500;
    font-size: 0.9rem;
}




.option-water:hover{
    background-color: #7BAF9E !important;
}

@media (min-width:801px) {
    body {
        overflow: hidden;
    }

    .container {
        width: 80%;
        height: 700px;
        margin: auto;
        display: grid;
        grid-template-areas:
            'logo image'
            'main image';
        grid-template-columns: 1fr 2fr;
        grid-template-rows: auto 3fr;
    }

    img.main-image-mobile {
        visibility: hidden;
        height: 0;
        width:0;
    }

    img.main-image {
        visibility: visible;
        max-width: 80vw;
        margin-top: 20px;
        margin-left: -100px;
    }

    .logo{
        padding: 3vw;
        margin: 0;
        min-width: 330px;
    } 
    
    .main{
        padding-left: 3vw;
        text-align: left;
    }

    .logo-image{
        width: 10rem;
        padding: 0;
    }

    .title {
        padding: 3rem 0 2rem;
    }

    button{
        margin: 0;
    }

    #btn-start:hover{
        cursor: pointer;
        outline: none;
        box-shadow: 1px 1px 9px -1px rgba(21,87,63,1);
    }
}

@media (max-width:800px) {
    body {
        overflow-x: hidden;
    }
    img.main-image {
        height: 0;
        width:0;
    }
}

.containerQuiz {
    margin: auto;
    width: 90%;
    width: 90vw;
    display: grid;
    grid-template-areas:
        'logo'
        'main'
        'options'
        'navigation';
}

.logo {grid-area: logo;}
.main {grid-area: main;}

.options {
    grid-area: options;
    text-align: center;
}

.navigation {
    grid-area: navigation;
    display: grid;
}

.main img {
    width: 35vw;
    max-width: 11rem;
}

.main h2{
    font-weight: 500;
    line-height: 2.5rem;
    padding: 1.6rem;
    padding-bottom: .5rem;
    font-size: 1.7rem;
    color: #6E6E6E;
    text-align: center;
}

.btn-home {
    margin: auto;
}

.optionButton {
    color: #6E6E6E;
    margin: auto;
    width: 80vw;
    border-radius: .5rem;
    padding: .8rem;
    margin-bottom: 1.5rem;
    background-color: #fff;
    box-shadow: 0px 21px 68px -14px rgba(110,110,110,0.71);
}

p{
    color: #6E6E6E;
    font-weight: 500;
    line-height: 2.5rem;
    padding: 0 1rem 1.6rem 1rem;
}

.buttonPrev {
    width: 7rem;
    text-align: center;
    border-radius: 3rem;
    padding: 1rem;
    color: #15573F;
    border: 1px solid #15573F;
    grid-area: prev;
}

.buttonNext {
    width: 7rem;
    text-align: center;
    border-radius: 3rem;
    padding: 1rem;
    color: #fff;
    background-color: #15573F;
    grid-area: next;
}

.selected.option-sun{
    background-color: #FD9872;
    color: #fff;
    font-weight: 700;
box-shadow: 0px 21px 27px -22px rgba(253,152,114,1);
}

.selected.option-pets{
    background-color: #FD9872;
    color: #fff;
    font-weight: 700;
box-shadow: 0px 21px 27px -22px rgba(253,152,114,1);
}
        
.selected.option-water{
    background-color: #7BAF9E;
    color: #fff;
    box-shadow: 1px 2px 9px -1px rgba(123,175,158,1);
}

@media (min-width: 801px){
    .containerQuiz{
        display: grid;
        width: 100vw;
        grid-template-areas:
            'logo main empty'
            'logo question empty'
            'logo navigation empty';
            grid-template-columns: 1fr 5fr 1fr;
    }

    .empty {
        grid-area: empty;
        width: 10vw;
        min-width: 10vw;
        max-width: 10vw;
    }
    .question{
        grid-area: question;
        display: flex;
        margin: auto;
        padding: 1rem;
    }

    .optionButton{
        padding: 1.6rem;
        margin: 1rem;
        width: 9rem;
        height: 8rem;
        font-size: .9rem;
    }

    .optionButton:hover{
        font-weight: 700;
        color: #fff;
        background-color: #FD9872;
    }

    .optionButton, .buttonNext, .buttonPrev:hover{
        cursor: pointer;
    }

    .logo-quiz{
        -webkit-transform: rotate(-90deg);
                transform: rotate(-90deg);
        padding: 0;
        margin: auto;
        margin-top: 10rem;
        width: 10vw;
        min-width: 10vw;
        max-width: 10vw;
    }

    .main-quiz{
        padding: 0;
        margin: auto;
    }

    .main-quiz img{
        display: flex;
        margin: auto;
        max-width: 8rem;
    }
    
    .main h2{
        max-width: 30rem;
    }

    .navigation{
        display: flex;
        justify-content: space-around;
        grid-template-areas:
        'prev next';
    }
    .buttonPrev:hover{
        background-color: #15573F;
        color: #fff;
    }

    .buttonNext{
        background-color: #fff;
        color: #15573F;
        border: 1px solid #15573F;
    }

    .buttonNext:hover{
        background-color: #15573F;
        color: #fff;
    }
    
}

@media (max-width:800px){
    
    .navigation {
        margin: auto;
        grid-template-areas:
            'next'
            'prev';
    }
    
    .buttonNext{
        margin: 1rem;
    }

    .buttonPrev{
        margin: 1rem;
    }
}
svg.iconPlantList{
    width: 24px;
    height: 24px;
}
.container-results {
    width: 100%;
    width:100vw;
    display: grid;
    grid-template-areas:
        'logo'
        'main'
        'plants';
}

.logo {grid-area: logo;}

.main {
    grid-area: main;
    padding-top: 2rem;
}
.plants {
    grid-area: plants;
    margin: auto;
}

h1{
    padding: 2rem;
    font-size: 3rem;
    font-weight: 800;
}

